<template>
  <div>
    <v-row>
      <v-col
        cols="12"
        md="12"
      >
        <material-card
          color="accent"
          full-header
        >
          <template #heading>
            <div
              class="pa-8 white--text text-h4"
            >
              Banner Form
            </div>
          </template>
          <v-container class="px-9">
            <v-row>
              <v-col
                cols="6"
                md="6"
              >
                <v-text-field
                  v-model="name"
                  label="Name"
                  required
                />
                <v-text-field
                  v-model="title"
                  label="Title"
                  required
                />
              </v-col>
              <v-col
                cols="6"
                md="6"
              >
                <v-text-field
                  v-model="caption"
                  label="Caption"
                  required
                />
                <v-file-input
                  v-model="image"
                  :rules="rules"
                  accept="image/jpeg"
                  prepend-icon="mdi-camera"
                  label="Photo"
                />
                <v-divider />
                <v-btn
                  dark
                  class="mt-4 float-right"
                  color="green"
                  @click="save"
                >
                  Save
                </v-btn>
              </v-col>
            </v-row>
          </v-container>
        </material-card>
        <material-card
          class="mt-8"
          color="accent"
          full-header
        >
          <template #heading>
            <div
              class="pa-8 white--text text-h4"
            >
              Banner List
            </div>
          </template>
          <v-data-table
            :headers="headers"
            :items="data"
            :items-per-page="5"
            class="elevation-1"
          >
            <template v-slot:item.banner_image="{ item }">
              <v-img
                width="150"
                height="200"
                :src="$baseUrl+item.banner_image"
              />
            </template>
            <template v-slot:item.status="{ item }">
              <v-chip
                v-if="item.status == 1"
                class="ma-2"
                color="green"
                text-color="white"
                x-small
                @click="updateStatusBanner(item.id)"
              >
                Active
              </v-chip>
              <v-chip
                v-else
                class="ma-2"
                color="red"
                text-color="white"
                x-small
                @click="updateStatusBanner(item.id)"
              >
                Non Active
              </v-chip>
            </template>
          </v-data-table>
        </material-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
  import ClassicEditor from '@ckeditor/ckeditor5-build-classic'
  import axios from 'axios'
  export default {
    data () {
      return {
        data: [],
        editor: ClassicEditor,
        editorData: 'Selamat Datang',
        editorConfig: {
          // The configuration of the editor.
        },
        title: null,
        caption: null,
        name: null,
        image: null,
        rules: [
          value =>
            !value ||
            value.size < 2000000 ||
            'Avatar size should be less than 2 MB!',
        ],
        headers: [
          {
            text: 'Name',
            value: 'name',
          },
          {
            text: 'Title',
            value: 'title',
          },
          {
            text: 'Caption',
            value: 'caption',
          },
          {
            text: 'Image',
            value: 'banner_image',
          },
          {
            text: 'Status',
            value: 'status',
          },
        ],
      }
    },

    mounted () {
      this.getData()
    },

    methods: {
      getData () {
        axios.get('admin/banner').then((res) => {
          this.data = res.data.data
        })
      },

      save () {
        this.$swal.showLoading()
        const form = new FormData()
        form.append('name', this.name)
        form.append('title', this.title)
        form.append('caption', this.caption)
        form.append('image', this.image)
        axios.post('admin/banner/save', form).then((res) => {
          this.getData()
          this.$swal('banner created successfully', '', 'success')
        })
      },

      updateStatusBanner (id) {
        axios.get('admin/banner/update-status/' + id).then(() => {
          this.getData()
          this.$swal('data updated successfully', '', 'success')
        })
      },
    },
  }
</script>
